import { defineStore, storeToRefs } from 'pinia';
import { computed, ComputedRef, ref, Ref } from 'vue';
import { useDbStore } from '@/stores/database';
import { useAccountsStore } from '@/stores/accounts';
import { useCategoriesStore } from '@/stores/categories';
import { useTransactionsStore } from '@/stores/transactions';

export const useStatusStore = defineStore('status', () => {
  const isGlobalLoading: Ref<boolean> = ref(false);
  const isDataNeeded: Ref<boolean> = ref(true);

  const isBaseReady: ComputedRef<boolean> = computed((): boolean => {
    const { userInfo } = storeToRefs(useDbStore());
    return userInfo.value.recordsId !== '';
  });

  const isAccountExist: ComputedRef<boolean> = computed((): boolean => {
    const { accounts } = storeToRefs(useAccountsStore());
    return accounts.value.length > 0;
  });

  const isCategoryExist: ComputedRef<boolean> = computed((): boolean => {
    const { categories } = storeToRefs(useCategoriesStore());
    return categories.value.length > 0;
  });

  const isTransactionExist: ComputedRef<boolean> = computed((): boolean => {
    const { transactions } = storeToRefs(useTransactionsStore());
    return transactions.value.length > 0;
  });

  const areBaseAndCategoriesReady: ComputedRef<boolean> = computed((): boolean => isBaseReady.value && isAccountExist.value && isCategoryExist.value);

  return { isGlobalLoading, isDataNeeded, areBaseAndCategoriesReady, isBaseReady, isAccountExist, isCategoryExist, isTransactionExist };
});