<template>
  <div id="modalEditAccount" ref="editAccountModal" class="modal">
    <div class="modal-content">
      <h4> 
        <strong>
          {{ selectedAccountName }}
        </strong></h4>
      <p>      </p>
      <p>    .</p>
      <button class="btn modal-close-btn modal-close"><i class="large material-icons">close</i>
      </button>
      <form class="mc-form-category">
        <div class="row">
          <div class="input-field col s12">
            <i class="material-icons prefix">mode_edit</i>
            <input
              id="accountNameEdit"
              v-model.trim="state.accountName"
              :class="{ invalid: isAccountNameRequiredInvalid || isAccountNameMinLengthInvalid }"
              type="text"
            >
            <label for="accountNameEdit">  </label>
            <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
            <small
              v-if="isAccountNameRequiredInvalid"
              class="helper-text red-text"
            >{{ v$.accountName.required.$message }}</small>
            <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
            <small
              v-else-if="isAccountNameMinLengthInvalid"
              class="helper-text red-text"
            >{{ v$.accountName.minLength.$message }}</small>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <i class="material-icons prefix">currency_ruble</i>
            <input
              id="amount"
              v-model.number="state.balance"
              step="0.01"
              type="number">
            <label for="amount"></label>
          </div>
          <div class="col s12 m6">
            <label>
              <input v-model="state.isCountable" class="filled-in" type="checkbox"/>
              <span>   </span>
            </label>
          </div>
        </div>
        <button class="mc-button-submit btn" name="action" type="submit"
                @click.prevent="submitForm">
          <i class="material-icons right">publish</i>
        </button>
      </form>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useVuelidate } from '@vuelidate/core';
import { required, minLength, helpers } from '@vuelidate/validators';
import { computed, reactive, ref, onBeforeUnmount, onMounted } from 'vue';
import { storeToRefs } from 'pinia';
import { Data, useAccountsStore } from '@/stores/accounts';

const editAccountModal: any = ref(null);
const modalInstance = ref();
const { selectedAccountName, selectedAccountBalance } = storeToRefs(useAccountsStore());
const { changeAccount } = useAccountsStore();

// validation setup
const nameMinLength = ref(2);
const state = reactive({
  accountName: '',
  balance: 0,
  isCountable: true
});
const rules = {
  accountName: {
    required: helpers.withMessage(
      '    .',
      required
    ),
    minLength: helpers.withMessage(
      ({ $params }) =>
        `      ,  ${ $params.min } .`,
      minLength(nameMinLength)
    )
  }
};
const v$ = useVuelidate(rules, state);
const isAccountNameRequiredInvalid = computed(() => {
  return v$.value.accountName.$dirty && v$.value.accountName.required.$invalid;
});
const isAccountNameMinLengthInvalid = computed(() => {
  return v$.value.accountName.$dirty && v$.value.accountName.minLength.$invalid;
});

const submitForm = async () => {
  if (v$.value.$invalid) {
    v$.value.$touch();
    return;
  }

  const formData: Data = {
    accountName: state.accountName,
    balance: state.balance,
    isCountable: state.isCountable
  };

  try {
    await changeAccount(formData);
  } catch (e) {
    console.error(e);
  } finally {
    state.accountName = '';
    modalInstance.value.close();
  }
};

// noinspection JSUnusedGlobalSymbols
const modalOptions = {
  onOpenStart: () => {
    state.accountName = selectedAccountName.value;
    state.balance = selectedAccountBalance.value;
  },
  onOpenEnd: () => M.updateTextFields()
};

onMounted(() => {
  modalInstance.value = M.Modal.init(editAccountModal.value, modalOptions);
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});
</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>