import PINIA from '@/stores/index';
import { defineStore } from 'pinia';
import { useAuthStore } from '@/stores/auth';
import { useAccountsStore } from '@/stores/accounts';
import { ref as refDb, child, get, update, set } from 'firebase/database';
import { db } from '@/utils/firebase.config';
import { computed, ComputedRef, reactive, Ref, ref } from 'vue';

const { getUid } = useAuthStore(PINIA);
const { subscribeToAccounts } = useAccountsStore(PINIA);

interface UserData {
  firstName: string,
  lastName: string,
  email: string,
  id: string,
  recordsId: string
}

export const useDbStore = defineStore('database', () => {
  const userInfo: UserData = reactive({
    firstName: '',
    lastName: '',
    email: '',
    id: '',
    recordsId: '' // Use this property to access /bases
  });
  const recordsIdAttach: Ref<string> = ref('');

  const baseId: ComputedRef<string> = computed((): string => userInfo.recordsId);
  const userFullName: ComputedRef<string> = computed((): string => `${ userInfo.firstName } ${ userInfo.lastName }`);

  async function createNewUserBase() {
    try {
      const uid = getUid();
      if (!!uid) {
        await set(refDb(db, `/bases/${ uid }/info/`), {
          totalBalance: 0
        });
        userInfo.recordsId = uid;
        const updates = {};
        updates[`/users/${ uid }/`] = userInfo;
        return update(refDb(db), updates);
      } else {
        console.error('UID is empty or null');
      }
    } catch (e) {
      console.error(e);
    } finally {
      await subscribeToAccounts();
    }
  }

  async function attachUserBase() {
    try {
      const uid = getUid();
      if (!!uid) {
        userInfo.recordsId = recordsIdAttach.value;
        const updates = {};
        updates[`/users/${ uid }/`] = userInfo;
        return update(refDb(db), updates);
      } else {
        console.error('UID is empty or null');
      }
    } catch (e) {
      console.error(e);
    } finally {
      await subscribeToAccounts();
    }
  }

  async function fetchUserInfo() {
    try {
      const uid: string | null = getUid();
      const currentUser = await (await get(child(refDb(db), `users/${ uid }/`))).val();
      if (currentUser && currentUser.id !== '') {
        userInfo.firstName = currentUser.firstName;
        userInfo.id = currentUser.id;
        userInfo.lastName = currentUser.lastName;
        userInfo.email = currentUser.email;
        userInfo.recordsId = currentUser.recordsId;
      } else {
        console.error('You are not logged in');
      }
    } catch (e) {
      console.error(e);
    }
  }

  return {
    userInfo,
    recordsIdAttach,
    baseId,
    userFullName,
    fetchUserInfo,
    attachUserBase,
    createNewUserBase
  };
});