<template>
  <div id="modalTransferBetweenAccounts" ref="modalTransfer" class="modal">
    <div class="modal-content">
      <h4>     </h4>
      <button class="btn modal-close-btn modal-close red"><i class="large material-icons">close</i>
      </button>
      <form class="mc-form-category">
        <div class="row">
          <div class="input-field col s12">
            <select v-model="selectedAccountId">
              <option v-for="acc in accounts" :key="acc.id" :value="acc.id">
                {{ acc.name }} | {{ currencyFilter(acc.balance) }}
              </option>
            </select>
            <label> -</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <select v-model="destinationAccountId">
              <option v-for="acc in accounts" :key="acc.id" :value="acc.id">
                {{ acc.name }} | {{ currencyFilter(acc.balance) }}
              </option>
            </select>
            <label> -</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <i class="material-icons prefix">calendar_month</i>
            <input
              id="transferDate"
              ref="datepicker"
              class="datepicker"
              name="transferDate"
              type="text">
            <label for="transferDate"></label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <i class="material-icons prefix">currency_ruble</i>
            <input
              id="amount"
              v-model="transferAmount"
              step="0.01"
              type="number">
            <label for="amount"> </label>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <small
              v-if="!selectedAccountId"
              class="red-text">
                -
            </small>
            <small
              v-else-if="!destinationAccountId"
              class="red-text">
                -
            </small>
            <small
              v-else-if="areAccountsSame"
              class="red-text">
              -  -   
            </small>
            <small
              v-else-if="isSourceAccountBalanceNotEnough"
              class="red-text">
               -  
              <strong>{{ currencyFilter(requiredAmountForTransfer.toString()) }}</strong>
            </small>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <button
              :disabled="isFormInvalid"
              class="mc-button-submit btn blue"
              name="action"
              type="submit"
              @click.prevent="submitFormTransfer">
              <i class="material-icons right">forward</i>
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref, onBeforeUnmount, onMounted, ComputedRef, computed } from 'vue';
import M from 'materialize-css';
import { storeToRefs } from 'pinia';
import { useAccountsStore } from '@/stores/accounts';
import { useFiltersStore } from '@/stores/filters';
import { FormDataTransactions, useTransactionsStore } from '@/stores/transactions';
import { datepickerOptions } from '@/utils/datepicker.options';

const {
  accounts,
  selectedAccountId,
  selectedAccountName,
  destinationAccountId,
  destinationAccountName,
  transferAmount,
  requiredAmountForTransfer,
  isSourceAccountBalanceNotEnough
} = storeToRefs(useAccountsStore());
const { pushNewTransaction } = useTransactionsStore();
const { transactionDate } = storeToRefs(useTransactionsStore());
const { currencyFilter } = useFiltersStore();

const modalTransfer = ref();
const modalInstance = ref();
const selectInstances = ref();
const datepicker = ref();
const datepickerInstance = ref();

const areAccountsSame: ComputedRef<boolean> = computed((): boolean => {
  return selectedAccountId.value === destinationAccountId.value ||
    !selectedAccountId.value || !destinationAccountId.value;
});

const isFormInvalid: ComputedRef<boolean> = computed((): boolean => {
  return areAccountsSame.value || isSourceAccountBalanceNotEnough.value || transferAmount.value <= 0;
});

const submitFormTransfer = async () => {
  if (isFormInvalid.value) return;

  const formData: FormDataTransactions = {
    account: selectedAccountName.value,
    accountId: selectedAccountId.value,
    amount: transferAmount.value,
    category: destinationAccountName.value,
    categoryId: destinationAccountId.value,
    date: transactionDate.value,
    description: `  ${ selectedAccountName.value }  ${ destinationAccountName.value }`,
    type: 'transfer'

  };

  await pushNewTransaction(formData);
  modalInstance.value.close();
};

onMounted(() => {
  modalInstance.value = M.Modal.init(modalTransfer.value);
  selectInstances.value = document.querySelectorAll('select');
  M.FormSelect.init(selectInstances.value);
  M.updateTextFields();
  datepickerInstance.value = M.Datepicker.init(datepicker.value, datepickerOptions);
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});

</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>