import { defineStore, storeToRefs } from 'pinia';
import PINIA from '@/stores/index';
import { auth, db } from '@/utils/firebase.config';
import { ref as refDb, set } from 'firebase/database';
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut
} from 'firebase/auth';
import router from '@/router';
import { computed, Ref, ref } from 'vue';
import { useAccountsStore } from '@/stores/accounts';
import { useDbStore } from '@/stores/database';
import { useStatusStore } from '@/stores/status';

export const useAuthStore = defineStore('auth', () => {
      const componentKey: Ref<number> = ref(0);

      function forceRerender() {
        componentKey.value += 1;
      }

      async function login({ email, password }) {
        const { subscribeToAccounts } = useAccountsStore(PINIA);
        const { fetchUserInfo } = useDbStore(PINIA);
        const { isGlobalLoading } = storeToRefs(useStatusStore(PINIA));

        try {
          isGlobalLoading.value = true;
          await signInWithEmailAndPassword(auth, email, password);
          await fetchUserInfo();
          await subscribeToAccounts();
          await router.push('/profile');
          forceRerender();
        } catch (e) {
          console.error(e);
        } finally {
          isGlobalLoading.value = false;
        }
      }

      async function register({ firstName, lastName, email, password }) {
        const { subscribeToAccounts } = useAccountsStore(PINIA);
        const { fetchUserInfo } = useDbStore(PINIA);
        const { isGlobalLoading } = storeToRefs(useStatusStore(PINIA));

        try {
          isGlobalLoading.value = true;
          await createUserWithEmailAndPassword(auth, email, password);
          const uid: string | null = getUid();
          if (uid) {
            await set(refDb(db, `/users/${ uid }/`), {
              firstName,
              lastName,
              email,
              id: uid,
              recordsId: ''
            });
          } else {
            console.error('UID is null or empty');
          }
          await fetchUserInfo();
          await subscribeToAccounts();
          await router.push('/profile');
          forceRerender();
        } catch (e) {
          console.error(e);
        } finally {
          isGlobalLoading.value = false;
        }
      }

      async function logout() {
        try {
          await signOut(auth);
          await router.push('/');
          forceRerender();
        } catch (e) {
        }
      }

      function getUid(): string | null {
        const user = auth.currentUser;
        return user ? user.uid : null;
      }

      return {
        register,
        getUid,
        login,
        logout,
        componentKey
      };
    }
  )
;