<template>
  <div id="modalSetLimit" ref="setLimitModal" class="modal">
    <div class="modal-content">
      <h4> </h4>
      <button
        class="btn modal-close-btn modal-close red">
        <i class="large material-icons">close</i>
      </button>
      <form class="mc-form-category">
        <div class="row">
          <div class="col s12">
            <div class="input-field col s12">
              <select v-model="selectedCategoryId">
                <option v-for="cat in categoriesAvailableForLimitation" :key="cat.id" :value="cat.id">
                  {{ cat.name }}
                </option>
              </select>
              <label></label>
            </div>
          </div>
        </div>
        <button class="mc-button-submit btn" name="action" type="submit">
           
          <i class="material-icons right">upload</i>
        </button>
      </form>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { onBeforeUnmount, onMounted, ref } from 'vue';
import { storeToRefs } from 'pinia';
import { useCategoriesStore } from '@/stores/categories';

const setLimitModal = ref();
const modalInstance = ref();

const { selectedCategoryId, categoriesAvailableForLimitation } = storeToRefs(useCategoriesStore());

onMounted(() => {
  modalInstance.value = M.Modal.init(setLimitModal.value);
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});
</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>
