<template>
  <div id="modalDeleteAccount" ref="deleteAccountModal" class="modal">
    <div class="modal-content">
      <h4>  {{ selectedAccountName }}</h4>
      <p>     ?</p>
      <p>        .</p>
      <button class="btn modal-close-btn modal-close"><i class="large material-icons">close</i>
      </button>
      <form class="mc-form-category">
        <div class="row">
          <div class="input-field col s12">
            <i class="material-icons prefix">mode_edit</i>
            <input
              @input="nameCheckHandler"
              id="deleteAccountNameField"
              v-model.trim="state.deleteAccount"
              :class="{ invalid: isDeleteAccountNameRequiredInvalid }"
              type="text"
            >
            <label for="deleteAccountNameField">  </label>
            <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
            <small
              v-if="isDeleteAccountNameRequiredInvalid"
              class="helper-text red-text"
            >{{ v$.deleteAccount.required.$message }}</small>
          </div>
        </div>
        <button :disabled="!readyToDelete" class="mc-button-submit btn red" name="action" type="submit"
                @click.prevent="deleteHandler"> 
          <i class="material-icons right">delete_forever</i>
        </button>
      </form>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, reactive, ref, onMounted, onBeforeUnmount } from 'vue';
import { helpers, required } from '@vuelidate/validators';
import { useVuelidate } from '@vuelidate/core';
import { useAccountsStore } from '@/stores/accounts';
import { storeToRefs } from 'pinia';

const readyToDelete = ref(false);
const deleteAccountModal: any = ref(null);
const modalInstance = ref();
const { deleteAccount } = useAccountsStore();
const { selectedAccountName } = storeToRefs(useAccountsStore());

const nameCheckHandler = () => {
  readyToDelete.value = state.deleteAccount === selectedAccountName.value;
};

const deleteHandler = () => {
  deleteAccount();
  modalInstance.value.close();
};

// validation setup
const state = reactive({
  deleteAccount: ''
});
const rules = {
  deleteAccount: {
    required: helpers.withMessage(
      '      ',
      required
    )
  }
};
const v$ = useVuelidate(rules, state);
const isDeleteAccountNameRequiredInvalid = computed(() => {
  return v$.value.deleteAccount.$dirty && v$.value.deleteAccount.required.$invalid;
});

onMounted(() => {
  modalInstance.value = M.Modal.init(deleteAccountModal.value);
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});
</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>