import { defineStore, storeToRefs } from 'pinia';
import { computed, ComputedRef, Ref, ref } from 'vue';
import { useDbStore } from '@/stores/database';
import { ref as refDb } from '@firebase/database';
import { db } from '@/utils/firebase.config';
import { get, onValue, push, set, update } from 'firebase/database';

type Transaction = {
  account: string,
  accountId: string,
  amount: number,
  author: string,
  category: string,
  categoryId: string,
  createdAt: string,
  date: string,
  dateForSort: number,
  description: string
  id: string,
  type: 'income' | 'outcome' | 'transfer'
}

export type FormDataTransactions = {
  account: string,
  accountId: string,
  amount: number,
  category: string,
  categoryId: string,
  date: Date,
  description: string,
  type: 'income' | 'outcome' | 'transfer'
}

export const useTransactionsStore = defineStore('transactions', () => {
  const transactionsData: Ref<Transaction[]> = ref([]);
  const selectedTransactionId: Ref<string> = ref('');
  const counter: Ref<number> = ref(0);
  const transactionDate: Ref<Date> = ref(new Date());

  const transactions: ComputedRef<Transaction[]> = computed(() => Object.values(transactionsData.value));

  const transactionsSortedByDate = computed(() => {
    return transactions.value.toSorted((a, b) => a.dateForSort - b.dateForSort);
  });

  const outcomeTransactionsInThisMonth: ComputedRef<Transaction[]> = computed(() => {
    return transactions.value.filter((tr) => tr.type === 'outcome')
      .filter((tr) => new Date(tr.date).getMonth() === new Date().getMonth());
  });

  const selectedTransaction: ComputedRef<Transaction | undefined> = computed(() => {
    return transactions.value.find((tr) => tr.id === selectedTransactionId.value);
  });

  const getDateNumber = (date: Date): number => {
    const yearStr: string = date.getFullYear().toString();
    const monthStr: string = (date.getMonth() + 1).toString();
    let dateStr: string = date.getDate().toString();
    if (dateStr.length < 2) {
      dateStr = '0' + dateStr;
    }
    return Number(yearStr.concat(monthStr, dateStr));
  };

  const getNewDate = (newDate: Date) => {
    transactionDate.value = newDate;
  };

  const pushNewTransaction = async (data: FormDataTransactions) => {
    const { baseId, userFullName } = storeToRefs(useDbStore());

    const newTransaction: Transaction = {
      account: data.account,
      accountId: data.accountId,
      amount: data.amount,
      author: userFullName.value,
      category: data.category,
      categoryId: data.categoryId,
      createdAt: new Date().toLocaleString(),
      date: data.date.toDateString(),
      dateForSort: getDateNumber(data.date),
      description: data.description,
      id: '',
      type: data.type
    };

    try {
      switch (newTransaction.type) {
        case 'transfer':
          await changeBalanceByTransfer(newTransaction, baseId.value);
          break;
        case 'outcome':
          await changeBalanceByOutcome(newTransaction, baseId.value);
          break;
        default:
          await changeBalanceByIncome(newTransaction, baseId.value);
          break;
      }
      const newTransactionRef = push(refDb(db, `bases/${ baseId.value }/transactions/`));
      newTransaction.id = <string>newTransactionRef.key;
      await set(newTransactionRef, newTransaction);
    } catch (e) {
      console.error(e);
    }
  };

  const changeBalanceByIncome = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance += data.amount;
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    return update(refDb(db), updates);
  };

  const changeBalanceByOutcome = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance -= data.amount;
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    return update(refDb(db), updates);
  };

  const changeBalanceByTransfer = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance -= data.amount;
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    const destinationAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.categoryId }`);
    const destinationAccountData = await (await get(destinationAccountRef)).val();
    destinationAccountData.balance += data.amount;
    updates[`bases/${ baseId }/accounts/${ data.categoryId }`] = destinationAccountData;
    return update(refDb(db), updates);
  };

  //TODO Add EDIT TRANSACTION
  //TODO Add DELETE TRANSACTION
  //TODO Add SUM BY CATEGORY
  //TODO Add SUM BY ACCOUNT

  const subscribeToTransactions = () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const transactionsRef = refDb(db, `bases/${ baseId.value }/transactions/`);
      onValue(transactionsRef, async (snapshot) => {
        if (snapshot.exists()) {
          transactionsData.value = snapshot.val();
        } else if (counter.value < 5) {
          setTimeout(() => {
            counter.value++;
            subscribeToTransactions();
          }, (counter.value + 1) * 100);
        } else {
          transactionsData.value = [];
          console.error('    ');
        }
      });
    } catch (e) {
      console.error(e);
    }
  };

  return {
    transactions,
    selectedTransactionId,
    selectedTransaction,
    transactionDate,
    transactionsSortedByDate,
    outcomeTransactionsInThisMonth,
    pushNewTransaction,
    subscribeToTransactions,
    getNewDate
  };
});