import { defineStore, storeToRefs } from 'pinia';
import {
  ref as refDb,
  onValue,
  push,
  set,
  update
} from 'firebase/database';
import { db } from '@/utils/firebase.config';
import { auth } from '@/utils/firebase.config';
import { computed, ComputedRef, Ref, ref } from 'vue';
import { useDbStore } from '@/stores/database';

type FormData = {
  categoryName: string,
  categoryType: string,
  categoryDescription: string,
  categoryId?: string | null,
  categoryAuthor?: string,
}

interface NewCategory {
  name: string,
  type: string,
  id: string | null | undefined,
  author: string,
  description: string,
}

export const useCategoriesStore = defineStore('categories', () => {
  const categoriesData: Ref<NewCategory[]> = ref([]);
  const selectedCategoryId: Ref<string> = ref('');
  const counter: Ref<number> = ref(0);

  const categories: ComputedRef<NewCategory[]> = computed(() => {
    return Object.values(categoriesData.value);
  });

  const categoriesAvailableForLimitation: ComputedRef<NewCategory[]> = computed(() => {
    return categories.value.filter((cat) => cat.type === 'outcome');
  });

  const selectedCategory = computed((): NewCategory | undefined => {
    return categories.value.find((category) => category.id === selectedCategoryId.value);
  });

  const pushNewCategory = async (data: FormData) => {
    const { baseId, userFullName } = storeToRefs(useDbStore());

    const newCategory: NewCategory = {
      name: data['categoryName'],
      type: data['categoryType'],
      id: '',
      author: userFullName.value,
      description: data['categoryDescription']
    };

    try {
      const newCategoryRef = push(refDb(db, `bases/${ baseId.value }/categories/`));
      if (typeof newCategoryRef.key === 'string') {
        newCategory.id = newCategoryRef.key;
      }
      await set(newCategoryRef, newCategory);
    } catch (e) {
      console.error(e);
    }
  };
  const changeCategory = async (data: FormData) => {
    data.categoryAuthor = <string>auth.currentUser?.displayName;

    const modifiedCategory: NewCategory = {
      name: data.categoryName,
      type: data.categoryType,
      id: data.categoryId,
      author: data.categoryAuthor,
      description: data.categoryDescription
    };

    try {
      const updates = {};
      const { baseId } = storeToRefs(useDbStore());
      updates[`bases/${ baseId.value }/categories/${ selectedCategoryId.value }`] = modifiedCategory;
      selectedCategoryId.value = '';
      return update(refDb(db), updates);
    } catch (e) {
      console.error(e);
    }
  };


  const deleteCategory = async () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const categoryRef = refDb(db, `bases/${ baseId.value }/categories/${ selectedCategoryId.value }`);
      selectedCategoryId.value = '';
      await set(categoryRef, null);
    } catch (e) {
      console.error(e);
    }
  };

  const subscribeToCategories = () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const categoriesRef = refDb(db, `bases/${ baseId.value }/categories/`);
      onValue(categoriesRef, async (snapshot) => {
        if (snapshot.exists()) {
          categoriesData.value = snapshot.val();
        } else if (counter.value < 5) {
          setTimeout(() => {
            counter.value++;
            subscribeToCategories();
          }, (counter.value + 1) * 100);
        } else {
          categoriesData.value = [];
          console.error('    ');
        }
      });
    } catch (e) {
      console.error(e);
    }
  };

  return {
    categories,
    categoriesAvailableForLimitation,
    selectedCategoryId,
    selectedCategory,
    pushNewCategory,
    changeCategory,
    deleteCategory,
    subscribeToCategories
  };
});