import { defineStore, storeToRefs } from 'pinia';
import { ref } from 'vue';
import { ref as refDb, onValue, set } from 'firebase/database';
import { db } from '@/utils/firebase.config';
import { useDbStore } from '@/stores/database';
import { useAccountsStore } from '@/stores/accounts';

export const useBalanceStore = defineStore('balance', () => {
  const totalBalance = ref(0);
  const counter = ref(0);

  const updateTotalBalance = async () => {
    try {
      const { allAccountsSum } = useAccountsStore();
      const { baseId } = storeToRefs(useDbStore());
      const balanceRef = refDb(db, `bases/${ baseId.value }/info`);
      await set(balanceRef, {
        totalBalance: allAccountsSum
      });
    } catch (e) {
      console.error(e);
    }
  };

  const subscribeToBalance = async () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const balanceRef = refDb(db, `bases/${ baseId.value }/info`);
      onValue(balanceRef, (snapshot) => {
        if (snapshot.exists()) {
          const data = snapshot.val();
          totalBalance.value = data.totalBalance;
        } else if (counter.value < 5) {
          setTimeout(() => {
            counter.value++;
            subscribeToBalance();
          }, (counter.value + 1) * 100);
        } else {
          totalBalance.value = 0;
          throw new Error('     ');
        }
      });
    } catch (e) {
      console.error(e);
    }
  };

  return { totalBalance, subscribeToBalance, updateTotalBalance };
});